$background-color: #e9f0fb;

body {
  margin: 0;
  padding: 0;
  background-color: $background-color;
}

header {
  position: absolute;
  display: flex;
  justify-content: space-between;
  align-items: center;
  left: 0;
  top: 30px;
  right: 0;
  margin: auto;
  width: 90%;
  height: 50px;
  z-index: 10;
}

aside {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  bottom: 40%;
  right: 0;
  left: 20px;
  width: 90%;
  height: auto;
  font-weight: 300;
  margin: auto;
  font-size: 40px;
  z-index: 0;
}

.title1 {
  width: 100%;
  margin: 0 0 20px 0;
  user-select: none;
  cursor: default;
}

.title2 {
  width: 100%;
  margin: 0 0 20px 0;
  user-select: none;
  cursor: pointer;
  &:before {
    content: "<";
    left: 0;
  }
  &:after {
    content: ">";
    right: 0;
  }
}

.description {
  white-space: normal;
  font-size: 17px;
  margin: 0;
  user-select: none;
  cursor: default;
}

#designer {
  width: 280px;
}

#coder {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 280px;
}

h1 {
  margin: 0 10px 0 10px;
  font-weight: 200;
  padding: 0;
  font-size: 18px;
  pointer-events: none;
  white-space: nowrap;
}

a {
  margin: 0 10px 0 10px;
  font-weight: 200;
  padding: 0;
  font-size: 17px;
  text-decoration: none;
  cursor: pointer;
  white-space: nowrap;
  color: black;
}

p {
  font-weight: 200;
  font-size: 11px;
  margin: 0 20px 0 20px;
  padding: 0;
  white-space: nowrap;
}

@media (max-width: 800px) {
  p,
  span {
    display: none;
  }
}

.information-container,
.contact-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  // margin: 0 20px 0 20px;
}

@media (max-width: 800px) {
  .contact-container {
    position: absolute;
    top: 78vh;
    left: 0;
    right: 0;
    margin: auto;
  }
}

