.colorPalette {
  position: absolute;
  width: 100vw;
  height: 100vh;
  top: 0;
  z-index: 0;
  transition: all 0.3s ease-in-out;
  border-radius: 0 320px 0 320px;
}

.colorPalette.active {
  background: linear-gradient(-45deg, #132226, #525b56, #a4978e, #be9063);
  background-size: 400% 400%;
  animation: gradient 15s ease infinite;
  @keyframes gradient {
    0% {
      background-position: 0% 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0% 50%;
    }
  }
}

.items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  &__inner {
    z-index: 2;
    top: 20px;
    left: -5px;
    position: relative;
    width: 550px;
    max-width: 800px;
    height: 620px;
    background: transparent;
  }
  &__layer &__item {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    pointer-events: all;
    box-shadow: 0 32px 40px -20px rgba(0, 0, 0, 0.25);
    &:after {
      content: attr(data-title);
      position: absolute;
      line-height: 20px;
      font-size: 10px;
      text-transform: uppercase;
      letter-spacing: 2px;
      transition: all 0.5s ease;
      visibility: hidden;
      opacity: 0;
    }
    &:hover {
      &:after {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  &__layer:nth-child(1) &__item {
    $color: #040c0e;
    top: 120px;
    left: 0px;
    width: 340px;
    height: 340px;
    background: $color;
    &:after {
      top: -23px;
      left: 0;
      color: $color;
    }
  }
  &__layer:nth-child(2) &__item {
    $color: #a4978e;
    top: 360px;
    left: 280px;
    width: 210px;
    height: 200px;
    background: $color;
    &:after {
      bottom: -23px;
      right: 0;
      color: $color;
    }
  }
  &__layer:nth-child(3) &__item {
    $color: #132226;
    top: 320px;
    left: 40px;
    width: 300px;
    height: 300px;
    background: $color;
    &:after {
      bottom: -23px;
      left: 0;
      color: $color;
    }
  }
  &__layer:nth-child(4) &__item {
    $color: #525b56;
    top: 10px;
    left: 280px;
    width: 270px;
    height: 480px;
    background: $color;
    &:after {
      top: -23px;
      right: 0;
      color: darken($color, 9%);
    }
  }
  &__layer:nth-child(5) &__item {
    $color: #be9063;
    top: 210px;
    left: 180px;
    width: 200px;
    height: 200px;
    background: $color;
    &:after {
      top: -220px;
      font-weight: bold;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 20px;
      line-height: 20px;
      margin: auto;
      text-align: center;
      color: darken($color, 12%);
    }
  }
}

@media (max-width: 800px) {
  .items {
    width: 100%;
    height: 100vh;
    &__inner {
      width: 500px;
      height: 480px;
    }
    &__layer:nth-child(1) &__item {
      width: 42vw;
      height: 25vh;
      left: 14vw;
      top: 10vh;
    }
    &__layer:nth-child(2) &__item {
      width: 29vw;
      height: 10vh;
      top: 40vh;
      left: 57vw;
    }
    &__layer:nth-child(3) &__item {
      width: 50vw;
      height: 21vh;
      left: 24vw;
      top: 25vh;
    }
    &__layer:nth-child(4) &__item {
      left: 54vw;
      width: 44vw;
      height: 37vh;
    }
    &__layer:nth-child(5) &__item {
      width: 35vw;
      height: 16vh;
      top: 17vh;
      left: 37vw;
      &:after {
        top: -150px;
      }
    }
  }
}

