.aboutMe-container {
  display: flex;
  position: absolute;
  margin: 110px 0 0 30px;
  font-size: 17px;
  left: 0px;
  width: 900px;
  flex-direction: column;
}
.aboutMe-container > div {
  overflow: hidden;
}
.aboutMe-container > div > p {
  width: 900px;
  margin: 0;
  line-height: 117%;
}

.green {
  color: #32c267;
}

div.aboutMe-container > div {
  -webkit-animation-delay: 0;
  animation-delay: 0;
}
div.aboutMe-container > div:nth-child(2) > p {
  -webkit-animation-delay: 0.05s;
  animation-delay: 0.05s;
}

div.aboutMe-container > div {
  -webkit-animation-delay: 0;
  animation-delay: 0;
}
div.aboutMe-container > div:nth-child(3) > p {
  -webkit-animation-delay: 0.075s;
  animation-delay: 0.075s;
}

.slide-up {
  -webkit-animation: slide-up 0.3s cubic-bezier(0.65, 0, 0.35, 1) both;
  animation: slide-up 0.3s cubic-bezier(0.65, 0, 0.35, 1) both;
}

@-webkit-keyframes slide-up {
  0% {
    transform: translateY(100px);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes slide-up {
  0% {
    transform: translateY(100px);
  }
  100% {
    transform: translateY(0);
  }
}

.content {
  user-select: none;
  cursor: default;
  display: flex;
  position: absolute;
  top: 120%;
  right: 27px;
  align-items: center;
  height: 20px;
  overflow: visible;
  font-size: 20px;
  line-height: 40px;
  color: black;
  justify-content: center;
  background-color: $background-color;

  &__container {
    font-weight: 600;
    overflow: hidden;
    height: 20px;
    padding: 0 50px;

    &:before {
      content: "[";
      right: 240px;
    }

    &:after {
      content: "]";
      position: absolute;
      right: 25px;
    }

    &:after,
    &:before {
      position: absolute;
      bottom: -9px;
      font-weight: bold;
      color: #525b56;
      font-size: 20px;
      line-height: 40px;

      -webkit-animation-name: opacity;
      -webkit-animation-duration: 2s;
      -webkit-animation-iteration-count: infinite;
      animation-name: opacity;
      animation-duration: 2s;
      animation-iteration-count: infinite;
    }

    &__text {
      position: absolute;
      display: inline;
      right: 147px;
      float: left;
      margin: 0;
      font-size: 17px;
    }

    &__list {
      margin-top: 44px;
      padding: 0;
      text-align: center;
